import React, { useEffect, useState } from "react";
import { Layout, Menu } from "antd";
import { Link } from "gatsby";
import midivalLogo from "../Banner/midival-logo.png";

export const Header = ({ showOnlyOnScroll }) => {
  const [isVisible, setIsVisible] = useState(0);
  useEffect(() => {
    if (!showOnlyOnScroll) {
      return;
    }
    const onScroll = (e) => {
      setIsVisible(window.scrollY > 200 ? 1 : 0);
    }
    window.addEventListener("scroll", onScroll);
    return () => {
      window.removeEventListener("scroll", onScroll);
    }
  }, [showOnlyOnScroll]);
  return (<Layout.Header className={`page-header ${showOnlyOnScroll && !isVisible ? 'hide' : ''}`} style={{ position: 'fixed', zIndex: 1, width: '100%', opacity: showOnlyOnScroll ? isVisible : 1 }}>
      <Menu mode="horizontal" style={{background: "transparent"}} className="header-menu">
        <Menu.Item key="logo"><Link to="/"><img src={midivalLogo} style={{maxHeight: '35px'}} alt="LOGO" /></Link></Menu.Item>
        <Menu.Item key="getting-started"><Link to="/docs/getting-started">Getting Started</Link></Menu.Item>
        <Menu.SubMenu key="midival-core" title="@midival/core">
          <Menu.Item><Link to="https://midival.github.io/core/">Documentation</Link></Menu.Item>
          <Menu.Item><Link to="https://github.com/midival/core/">GitHub</Link></Menu.Item>
        </Menu.SubMenu>
        <Menu.SubMenu key="midival-node" title="@midival/node">
          <Menu.Item key="midival-node-doc"><Link to="https://midival.github.io/node/">Documentation</Link></Menu.Item>
          <Menu.Item key="midival-node-gh"><Link to="https://github.com/midival/node/">GitHub</Link></Menu.Item>
        </Menu.SubMenu>
        <Menu.SubMenu key="midival-react-native" title="@midival/react-native">
          <Menu.Item disabled key="midival-react-native-doc">Documentation</Menu.Item>
          <Menu.Item key="midival-react-native-gh"><Link to="https://github.com/midival/react-native/">GitHub</Link></Menu.Item>
        </Menu.SubMenu>
      </Menu>
  </Layout.Header>);
}